// –ò–ú–ü–û–†–¢ –î–ê–ù–ù–´–• –î–õ–Ø –°–û–ó–î–ê–ù–ò–Ø –†–ê–ó–ú–ï–¢–ö–ò (data)
import data from '../data/module-8.js'
// console.log(data)
// –ò–ú–ü–û–†–¢ –î–û–°–¢–£–ü–û–í –ö DOM-–≠–õ–ï–ú–ï–ù–¢–ê–ú (refs)
import refs from '../references/refs.js'
const { list, modal, closeBtn, modalContent } = refs
// console.log(list, modal, form)
// –®–ê–ë–õ–û–ù –†–ê–ó–ú–ï–¢–ö–ò
const item = `
        <li class="item">
          <div class="imgWrapper">
            <img src="https://images.pexels.com/photos/3024453/pexels-photo-3024453.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500" alt="img" />
          </div>
          <div class="cardInfo">
            <h3 class="title">Thousands</h3>
            <p class="text">
              Thousands of good, calm, bourgeois faces thronged the windows, the
              doors, the dormer windows, the roofs, gazing at the palace, gazing
              at the populace, and asking nothing more; for many Parisians
              content themselves with the spectacle of the spectators, and a
              wall behind which something is going on becomes at once, for us, a
              very curious thing indeed.
            </p>
            <button class="link" type="button">open here</button>
          </div>
        </li> 
`
// –§–£–ù–ö–¶–ò–Ø –°–û–ó–î–ê–ù–ò–Ø –†–ê–ó–ú–ï–¢–ö–ò
function createItems(array) {
  return array
    .map((elem) => {
      const { image, title = 'TITLE', text, button, id } = elem
      // console.log(image, title, text, button)
      return `
        <li class="item">
          <div class="imgWrapper">
            <img src=${image} alt=${title} />
          </div>
          <div class="cardInfo">
            <h3 class="title">${title}</h3>
            <p class="text">${text}</p>
            <button id=${id} class="link" type="button">${button}</button>
          </div>
        </li>`
    })
    .join('')
}
// –≤—ã–∑—ã–≤–∞–µ–º createItems, –ø–µ—Ä–µ–¥–∞–µ–º –¥–∞–Ω–Ω—ã–µ –∏ –¥–∂–æ–∏–Ω–∏–º
const markup = createItems(data)
// console.log(markup)

// –í–°–¢–†–ê–ò–í–ê–ù–ò–ï –°–û–ó–î–ê–ù–ù–û–ô –†–ê–ó–ú–ï–¢–ö–ò
list.insertAdjacentHTML('afterbegin', markup)
// –°–û–ó–î–ê–ï–ú –ù–ê–ë–õ–Æ–î–ê–¢–ï–õ–Ø

// 1 - —Å–æ–∑–¥–∞–µ–º —ç–∫–∑–µ–º–ø–ª—è—Ä Observer
const options = {
  root: list, // –æ—Ç–Ω–æ—Å–∏—Ç–µ–ª—å–Ω–æ –∫–æ–≥–æ –ø—Ä–æ–≤–µ—Ä—è–µ–º –≤–∏–¥–∏–º–æ—Å—Ç—å —ç–ª–µ–º–µ–Ω—Ç–∞
  rootMargin: '0px', // –≤–Ω–µ—à–Ω–∏–µ –æ—Ç—Å—Ç—É–ø—ã root, –ø–æ—ç—Ç–æ–º—É
  // –∑–∞–¥–∞–µ–º —Å -
  threshold: 0.5, // –Ω–∞ —Å–∫–æ–ª—å–∫–æ –ø—Ä–æ–≤–µ—Ä—è–µ–º—ã–π —ç–ª–µ–º–µ–Ω—Ç –¥–æ–ª–∂–µ–Ω
  // –ø–æ–ø–∞—Å—Ç—å –≤ –∑–æ–Ω—É –≤–∏–¥–∏–º–æ—Å—Ç–∏ root
}
const observer = new IntersectionObserver(callback, options)
function callback(entries) {
  entries.forEach((entry) => {
    entry.isIntersecting
      ? entry.target.classList.add('observe')
      : entry.target.classList.remove('observe')
  })
}

// 2 - –ø–æ–ª—É—á–∞–µ–º –º–∞—Å—Å–∏–≤ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ items, –∫ –∫–æ—Ç–æ—Ä—ã–º –ø—Ä–∏–º–µ–Ω–∏–º Observer (...list.children)
const items = [...list.children]

// 3 - –≤—ã–∑—ã–≤–∞–µ–º —ç–∫–∑–µ–º–ø–ª—è—Ä –Ω–∞–±–ª—é–¥–∞—Ç–µ–ª—è –Ω–∞ –∫–∞–∂–¥–æ–º —ç–ª–µ–º–µ–Ω—Ç–µ —Å–ø–∏—Å–∫–∞
items.forEach((item) => observer.observe(item))

// –î–û–ë–ê–í–õ–Ø–ï–ú –û–¢–ö–†–´–¢–ò–ï –ú–û–î–ê–õ–¨–ù–û–ì–û –û–ö–ù–ê

function showElement(element) {
  element.classList.remove('isHidden')
}

function hideElement(element) {
  element.classList.add('isHidden')
}

list.addEventListener('click', (e) => {
  const condition = e.target.nodeName === 'BUTTON'
  if (condition) {
    // modal.classList.remove('isHidden')
    showElement(modal)
    const modalImage = document.createElement('img')
    const link = data.find((object) => object.id === e.target.id).image
    modalImage.setAttribute('src', link)
    console.log(modalImage)

    modalContent.append(modalImage)
  }
})
// –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
//  –ø–æ –±–µ–∫–¥—Ä–æ–ø—É
modal.addEventListener('click', closeModalByClick)
function closeModalByClick(e) {
  if (e.target.classList.contains('closeModal')) {
    hideElement(modal)
  }
}
//  –ø–æ Escape ArrowLeft ArrowRight
window.addEventListener('keydown', closeModalByKey)
function closeModalByKey(e) {
  if (
    e.code === 'Escape' ||
    e.code === 'ArrowLeft' ||
    e.code === 'ArrowRight'
  ) {
    hideElement(modal)
  }
}
//  –ø–æ –∫–Ω–æ–ø–∫–µ –•
closeBtn.addEventListener('click', closeModalByClick)

// –∑–∞—á–∏—â–∞–µ–º —Å–ª—É—à–∞—Ç–µ–ª—è
if (!modal.classList.contains('isHidden')) {
  window.removeEventListener('keydown', closeModalByKey)
  modal.removeEventListener('click', closeModalByClick)
}
